import React from 'react';
import BreadCumb from '../../../Components/Common/BreadCumb';
// import TourDetails from '../../../Components/Umrah Detail/UmrahDetail';
import UmrahPackages from '../../../Components/Umrah-Package/UmrahPackages';
import UmrahDetails from '../../../Components/Umrah-Detail/UmrahDetail';
import { useParams } from "next/navigation";
import HajjDetails from '../../../Components/Hajj-Detail/HajjDetail';
const page = () => {
  const { id } = useParams();
  return (
    <div>
              <BreadCumb
                bgimg="/assets/img/breadcrumb/breadcrumb.jpg"
                Title="Hajj Detail"
            ></BreadCumb>    
            <HajjDetails packageId={Number(id)} />        
    </div>
  );
};

export default page;